* {
  box-sizing: border-box;
  outline: none;
  padding: 0;
  margin: 0;
}

button {
  border: none;
  box-shadow: none;
  cursor: pointer;
}

html {
  overflow-x: hidden;
  width: 100%;
  max-width: 100%;
}

body {
  overflow: hidden;
  background: #ffffff;
  min-height: 100vh;
}

html.freeze {
  overflow-y: hidden;
}

body.freeze {
  height: 100%;
  position: fixed;
  overflow-y: hidden;
  top: 0;
  width: 100%;
  left: 0;
}

h1,
h2 {
  font-family: "Chillax";
  font-weight: 500;
}

h3,
h4,
h5,
h6 {
  font-family: "Manrope";
  font-weight: 500;
}

h1 {
  font-weight: 500;
  font-size: 48px;
  line-height: 50px;
  color: white;
}
@media (max-width: 1920px) {
  h1 {
    font-size: 96px;
    line-height: 100px;
  }
}
@media (max-width: 1440px) {
  h1 {
    font-size: 62px;
    line-height: 70px;
  }
}
@media (max-width: 1024px) {
  h1 {
    font-size: 48px;
    line-height: 50px;
  }
}

body {
  font-family: "Manrope";
  background-color: #121316;
  color: white;
}

button {
  font-family: "Chillax";
  padding: 1em 2em;
  border-radius: 20em;
}

/**********************************/
/*    GLOBAL COMPONENT STYLES     */
/**********************************/
.site-header {
  background-color: #121316;
  color: white;
  padding: 1rem;
}
.site-header .header-desktop {
  display: flex;
  align-items: center;
  width: 100%;
}
.site-header .header-desktop .site-branding {
  margin-right: auto;
}
.site-header .header-desktop .site-branding img {
  width: 100px;
}
.site-header .header-desktop .main-navigation {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}
@media (max-width: 768px) {
  .site-header .header-desktop .main-navigation {
    display: none;
  }
}
.site-header .header-desktop .main-navigation #primary-menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  align-items: center;
}
.site-header .header-desktop .main-navigation #primary-menu li {
  position: relative;
}
.site-header .header-desktop .main-navigation #primary-menu li:has(ul.sub-menu) > a::after {
  content: "";
  display: inline-block;
  width: 0.3rem;
  height: 0.3rem;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  transform: rotate(45deg);
  margin-left: 0.5rem;
  margin-bottom: 0.2rem;
  transition: transform 0.3s;
}
.site-header .header-desktop .main-navigation #primary-menu li a {
  text-decoration: none;
  color: white;
  padding: 0.5rem 1rem;
}
.site-header .header-desktop .main-navigation #primary-menu li a:hover {
  color: #f4a4ec;
  transition: color 0.3s;
}
.site-header .header-desktop .main-navigation #primary-menu li ul.sub-menu {
  display: none;
  position: absolute;
  background-color: #1f2126;
  list-style: none;
  padding: 0.5rem;
  border-radius: 12px;
  white-space: nowrap;
  width: auto;
  padding: 0;
}
.site-header .header-desktop .main-navigation #primary-menu li ul.sub-menu li {
  margin-left: 0;
  font-size: 18px;
  cursor: pointer;
  border-bottom: solid 1px #393b44;
  padding: 12px 20px;
}
.site-header .header-desktop .main-navigation #primary-menu li ul.sub-menu li a {
  padding: 0;
}
.site-header .header-desktop .main-navigation #primary-menu li ul.sub-menu li:hover > a {
  color: #f4a4ec;
  transition: color 0.3s;
}
.site-header .header-desktop .main-navigation #primary-menu li ul.sub-menu li small {
  display: block;
  margin: 0;
  font-size: 14px;
  color: #fff;
  opacity: 0.5;
}
.site-header .header-desktop .main-navigation #primary-menu li ul.sub-menu li:last-child {
  border-bottom: none;
}
.site-header .header-desktop .main-navigation #primary-menu li:hover > ul.sub-menu {
  display: block;
}
.site-header .header-desktop .header-buttons {
  display: flex;
  align-items: center;
  margin-left: auto;
}
@media (max-width: 768px) {
  .site-header .header-desktop .header-buttons {
    display: none;
  }
}
.site-header .header-desktop .header-buttons button {
  background: none;
  color: white;
  margin-left: 1rem;
  padding: 0.75rem 1rem;
}
.site-header .header-desktop .header-buttons button:hover {
  color: #f4a4ec;
  transition: color 0.3s;
}
.site-header .header-desktop .header-buttons button.language-switcher {
  display: flex;
  align-items: center;
}
.site-header .header-desktop .header-buttons button.language-switcher img {
  width: 1.25rem;
  margin-right: 0.5rem;
}
.site-header .header-desktop .header-buttons button.language-switcher i {
  margin-left: 0.5rem;
  font-size: 0.5rem;
}
.site-header .header-desktop .header-buttons button.get-started {
  background-color: #e2d26c;
  color: black;
  border-radius: 20rem;
}
.site-header .header-desktop .header-buttons button.get-started:hover {
  background-color: #d9be11;
  transition: background-color 0.3s;
}
.site-header .header-desktop .hamburger-menu {
  display: none;
  background-color: #1f2126;
  color: white;
  border: solid 1px #1f2126;
  border-radius: 12px;
  padding: 0.6rem 0.75rem;
  line-height: 0rem;
  cursor: pointer;
}
@media (max-width: 768px) {
  .site-header .header-desktop .hamburger-menu {
    display: block;
  }
}
.site-header .header-desktop .hamburger-menu svg {
  width: 24px;
  height: 24px;
}
.site-header .header-desktop .hamburger-menu:hover {
  color: #f4a4ec;
  transition: color 0.3s;
}
.site-header::after {
  content: "";
  display: table;
  clear: both;
}
.site-header .mobile-navigation {
  display: none;
  flex-direction: column;
  align-items: left;
  width: 100%;
  height: calc(100vh - 76.8px);
  justify-content: space-between;
  position: absolute;
  top: 76.8px;
  left: 0;
  right: 0;
  padding: 1rem;
  background-color: #121316;
  z-index: 1000;
  overflow-y: auto;
}
.site-header .mobile-navigation.active {
  display: none;
}
@media (max-width: 768px) {
  .site-header .mobile-navigation.active {
    display: flex;
  }
}
.site-header .mobile-navigation ul {
  list-style: none;
}
.site-header .mobile-navigation ul a {
  text-decoration: none;
  color: white;
}
.site-header .mobile-navigation ul li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  border-bottom: solid 1px #1f2126;
  padding: 1rem 0rem;
  font-size: 20px;
}
.site-header .mobile-navigation ul li:hover > a {
  color: #f4a4ec;
  transition: color 0.3s;
}
.site-header .mobile-navigation ul li:has(ul.sub-menu) > a {
  flex-grow: 1;
}
.site-header .mobile-navigation ul li:has(ul.sub-menu) > a::after {
  content: "";
  display: inline-block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  transform: rotate(315deg);
  position: absolute;
  right: 1rem;
  margin-bottom: 0.15rem;
  transition: transform 0.3s;
}
.site-header .mobile-navigation ul li ul.sub-menu {
  display: none;
}
.site-header .mobile-navigation .header-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  width: 100%;
}
.site-header .mobile-navigation .header-buttons button {
  width: 100%;
  padding: 1rem 2rem;
  font-size: 20px;
  border-radius: 20rem;
}
.site-header .mobile-navigation .header-buttons button.language-switcher {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: none;
  border: 1px solid #1f2126;
  border-radius: 12px;
  color: white;
  margin-bottom: 2rem;
}
.site-header .mobile-navigation .header-buttons button.language-switcher img {
  width: 1.25rem;
  margin-right: 0.5rem;
}
.site-header .mobile-navigation .header-buttons button.language-switcher span {
  margin-right: auto;
}
.site-header .mobile-navigation .header-buttons button.language-switcher i {
  margin-left: 0.5rem;
  font-size: 16px;
}
.site-header .mobile-navigation .header-buttons button.sign-in {
  background-color: #1f2126;
  color: white;
}
.site-header .mobile-navigation .header-buttons button.get-started {
  background-color: #e2d26c;
  color: black;
  border-radius: 20rem;
}
.site-header .mobile-navigation .header-buttons button.get-started:hover {
  background-color: #d9be11;
  transition: background-color 0.3s;
}

